B,kDAkCI,eAAgB,CAChB,aAAc,CAnClB,yBAuCI,eAAgB,CAChB,eAAgB,CAChB,iBAAkB,CPzMlB,sCOgKJ,yBA4CM,eAAgB,CAEnB,CAEH,cACE,gBAAiB","sourcesContent":["@font-face {\n  font-family: 'Raleway';\n  font-style: normal;\n  font-weight: 400 800;\n  src:\n    url('../../assets/font/Raleway-VariableFont_wght.woff2') format('woff2');\n}\n\n*,\n*::before,\n*::after{\n  margin: 0;\n  padding: 0;\n  box-sizing: inherit;\n}\n\nbutton{\n  font-size: inherit;\n  font-family: inherit;\n  font-weight: inherit;\n}\n\nhtml{\n  font-size: 62.5%;\n  font-family: \"Raleway\", sans-serif;\n  text-rendering: optimizeLegibility;\n\n  background-color: $menu-background-color;\n  \n  //breakpoints for reasonable resizing of fonts on other devices\n  @include respond(tab-land){  // width < 1200\n    font-size: 56.25%;\n  }\n  @include respond(tab-port){  // width < 900?\n    font-size: 50%;\n  }\n  @include respond(phone){\n    font-size: 40%;\n    letter-spacing: 0.01rem;\n  }\n  \n  @include respond(desktop){\n    font-size: 66%;\n  }\n  @include respond(big-desk){  // width > 1800\n    font-size: 75%;\n  }\n}\n\nli{\n  list-style: none;\n}\n\n.app-div{\n  display: flex;\n  flex-flow: column nowrap;\n  align-items: center;\n}\n\n","$light-font-visited: #dddddd;\n$light-font-hover: #ffffff;\n\n$alt-dark-font: #201713;\n$dark-font: #2d2d2d;\n$soft-black: #424242;\n$button-font-shadow: #787878;\n$button-font-size: 2.5rem;\n$button-font-outline: #787878e6;\n\n$bright-brown: #9e4334;\n$less-bright-brown: #a0170f;\n$medium-brown: #94241e;\n$brown-brown: #6a2008;\n\n$menu-background-color: #f7f3e3;\n\n$portland-beige: #babd9b;\n$portland-off-white: #efeeec;\n$portland-red: #bd141b;\n$portland-red-alpha: #bd141ad7;\n\n//////////////////////////////////\n/////// NEW COLOR SCHEMES\n$portland-mustard: #bdb714;\n$dull-orange: #bd6314;\n\n$portland-pink: #bd146e;\n$portland-dull-red: #a33236;\n$potential-blue: #146ebd;\n\n$maybe-green: #70bd14;\n\n/////////////////////// \n//// try this \n// #bd141b, #f7f3e3, #ecf0f1, #2b2118, #6a5d7b\n$nu-beige: #f7f3e3;\n$fresh-walls: #ecf0f1;\n$brown-q: #2b2118; \n$faded-lavender: #6a5d7b;\n$goth-lavender: #625671;\n\n// PHONE STYLING\n$phone-nav-height: 10rem;\n$phone-nav-fontsize: 2.2rem;\n$phone-nav-logo-height: 7.5rem;\n\n$phone-item-name-fontsize: 2.2rem; \n$phone-item-notes-fontsize: 1.8rem;\n$phone-item-price-fontsize: 2.1rem;\n$phone-item-price-dot: 0.7rem;\n$phone-item-logo-size: 2.2rem;\n\n$phone-section-fontsize: 3.5rem;\n$phone-side-margin: 1.6rem;\n\n// TABLET STYLING\n\n$mobile-nav-height: 9rem;\n$mobile-nav-fontsize: 3.6rem;\n\n$mobile-section-fontsize: 4rem;\n$mobile-sub-section-fontsize: 3rem;\n\n$mobile-item-name-fontsize: 2rem;\n$mobile-item-notes-fontsize: 1.7rem;\n$mobile-item-price-fontsize: 1.9rem;\n$mobile-item-price-dot: 0.6rem;\n$mobile-side-margin: 2.5rem;\n\n$mobile-menu-width: 100%;\n\n// DESKTOP STYLING\n\n$desktop-pad-top: 4rem;\n$desktop-nav-height: 15rem;\n$desktop-nav-element-height: 10rem;\n$desktop-nav-logo-height: 8rem;\n\n$desktop-section-fontsize: 2.2rem;\n$desktop-sub-section-fontsize: 1.5rem;\n\n$desktop-item-base-fontsize: 1.5rem;\n$desktop-item-name-fontsize: 1.8rem;\n$desktop-item-notes-fontsize: 1.4rem;\n$desktop-item-price-fontsize: 1.6rem;\n$desktop-item-price-dot: 0.5rem;\n$desktop-item-logo-size: 1.4rem;\n\n$desktop-navhat-side-pad: 3rem;\n$desktop-side-margin: 20rem;\n// $desktop-menu-width: 76vw;\n\n$desktop-secondary-nav-top: 12rem;\n$desktop-secondary-nav-height: 11rem;\n","// generalized responsive design breakpoints\n@mixin respond($breakpoint){\n  @if $breakpoint == small-phone {\n    @media screen and (max-width: 25em) { @content };\n  }\n  @if $breakpoint == phone {\n    @media screen and (max-width: 37.5em) { @content }; // 600px\n  }\n  @if $breakpoint == tab-port {\n    @media screen and (max-width: 56.25em){ @content }; // 900px | 1em = 38.4px\n  }\n  @if $breakpoint == tab-land {\n    @media screen and (max-width: 75em) { @content }; // up to 750px? 1200px | 1em = 48px\n  }\n  @if $breakpoint == desktop { // 760 - 1120?\n    @media (min-width: 76em) and (max-width: 112em){ @content };\n  }\n  @if $breakpoint == m-desktop { //1130 - 1240?\n    @media (min-width: 113em ) and (max-width: 124em){ @content };\n  }\n  @if $breakpoint == big-desk {\n    @media screen and (min-width: 125em) { @content }; // 1800px?\n  }\n}\n\n@mixin fontShadow(){\n  text-shadow: \n    0.3rem 0.3rem $button-font-shadow, \n    -1px 0 1px $button-font-outline, \n    0 -1px 1px $button-font-outline;\n}\n\n@mixin newHeadingShadow(){\n  color: white;\n  text-shadow: 0.06em 0em 0.03em $goth-lavender, 0.1em 0 $potential-blue;\n}\n\n@mixin desktopPseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1.5rem;\n\n  border: 0.3rem solid $portland-red;\n  height: 0.2rem;\n  width: 88%;\n  margin: -3rem -44%;\n}\n\n@mixin mobilePseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border: 0.4rem solid $portland-red;\n  height: 0.3rem;\n  width: 90%;\n  margin: -2rem -45%;\n}\n\n@mixin singleLinePseudoDivider($width){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border-top: 0.4rem solid $portland-red;  \n  width: $width or 90%;\n  margin: -2rem (-$width/2 or -45%);\n}\n\n@mixin sectionBorderTop(){\n  border-top: 3px solid $portland-red;\n}",".nav-hat-wrapper{\n  position: absolute;\n  width: 100vw;\n  top: 0;\n  overflow: hidden;\n\n  button {\n    border: none;\n    cursor: pointer;\n    background: none;\n    text-align: center;\n\n    font-size: 2.6em;\n   \n    @include newHeadingShadow();\n\n  }\n\n  button:hover {\n    color: $light-font-hover;\n  }\n\n  button:active{\n    color: $portland-off-white;\n    transform: translateY(0.1rem);\n  }\n\n  nav{\n    .logo{\n      position: relative;\n      height: auto;\n      width: auto;\n      margin: 0 $desktop-side-margin/2 0 $desktop-side-margin;\n  \n      @include respond(phone){\n        height: $phone-nav-logo-height;\n        padding: 0;\n        margin-right: 0;\n        margin-left: $phone-side-margin;\n      }\n    }\n\n    .button-group{\n      position: relative;\n      margin: 0 $desktop-side-margin 0 $desktop-side-margin;\n      width: 100%;\n\n      display: flex;\n      flex-flow: row;\n      justify-content: space-between;\n  \n      font-weight: 700;\n      font-family: inherit;\n    }\n  }  \n}\nnav{ z-index: 1;}\nnav div{ z-index: 2;}\n\nbutton.button-current-page{\n  border-bottom: 3px solid $portland-red;\n  box-sizing: content-box;\n  margin-bottom: -3px;\n}\n",".full-nav{\n  position: fixed;\n  box-sizing: content-box;\n  top: 0;\n\n  width: inherit;\n  \n  height: $desktop-nav-height;\n  display: flex;\n  flex-flow: row;\n  justify-content: space-between;\n  align-items: center;\n\n  @include respond(tab-port){\n    display: none;\n  }\n}\n\n.bknd-img-wrapper{\n  position: absolute;\n  width: 100%;\n  height: $desktop-nav-height;\n  overflow: hidden;\n\n  @include respond(big-desk){\n    background-color: purple;\n  }\n\n  img{\n    object-fit: cover;\n    width: 110%;\n    filter: brightness(45%);\n    transform: translate(-10rem, -52rem) rotate(-12deg);\n\n    @include respond(desktop){\n      transform: translate(-10rem, -58rem) rotate(-12deg);\n    }\n  }\n}\n\n.secondary-nav-scroll-target{\n  position: absolute;\n  top: $desktop-secondary-nav-top;\n\n  @include respond(tab-port){\n    display: none;\n  }\n}\n\n.secondary-nav{\n  position: fixed;\n  top: -$desktop-secondary-nav-height ;\n\n  display: flex;\n  flex-flow: row;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: $desktop-secondary-nav-height;\n  background-color: $menu-background-color;\n  transition: transform 0.3s;\n\n  @include respond(tab-port){\n    display: none;\n  }\n\n  .bknd-img-wrapper{\n    height: $desktop-secondary-nav-height;\n\n    & img{\n      transform: translate(0rem, -17rem);\n    }\n  }\n}\n\n.secondary-nav-open{\n  transform: translateY(100%);\n  @include respond(tab-port){\n    display: none;\n  }\n}",".nav-hat-wrapper .mobile-nav{\n  position: fixed;\n  top: 0;\n  width: 100vw;\n\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: stretch;\n  \n  align-items: center;\n  height: $mobile-nav-height;\n\n  background-color: $alt-dark-font;\n\n  &__logo.logo{\n    display: inline-block;\n    height: $mobile-nav-height - 1.6rem;\n    margin: 0 0 0 $mobile-side-margin;\n\n    background-color: $alt-dark-font;\n\n    @include respond(phone){\n      height: $phone-nav-logo-height;\n      margin: 0.5rem 0 0.5rem $phone-side-margin;\n    }\n\n    @include respond(small-phone){\n      height: $phone-nav-logo-height - 0.8rem;\n      margin: 0.5rem 0 0.5rem ($phone-side-margin)-0.5rem;\n    }\n\n    img{\n      height: 100%;\n    }\n  } \n\n  &__button-group.button-group{\n    display: flex;\n    flex-flow: row nowrap;\n    align-items: center;\n    justify-content: space-between;\n    width: 100%;\n    height: 100%;\n\n    margin: 0;\n\n    @include respond(tab-land){\n      margin: 0 ($mobile-side-margin + 1rem );\n    }\n    \n    @include respond(phone){\n      margin-right: $phone-side-margin;\n      margin-left: $phone-side-margin*2;\n    }\n\n    @include respond(small-phone){\n      margin-left: $phone-side-margin;\n      margin-right: $phone-side-margin - 0.5rem;\n    }\n    \n    button{\n      font-size: 2.6em;\n      background-color: $alt-dark-font;\n\n      // @include newHeadingShadow();\n      \n      @include respond(phone){\n        margin-left: 0.5rem;\n        margin-top: 0.6rem;\n\n        font-size: 2.4em;\n      }\n\n      @include respond(small-phone){\n        font-size: 2em;\n      }\n    }\n  }\n}","/////////////////////////////// \n////// SECTIONS\n.food-menu{ // div at top\n\n  position: relative;\n  top: $desktop-nav-height; \n  margin: 1rem auto 3rem auto;\n  width: 76vw;\n\n  display: grid;\n  grid-template-columns: 50% 50%;\n\n  background-color: $menu-background-color;\n  color: $brown-q;\n  font-size: 2.5rem;\n\n  @include respond(tab-port){\n    top: $mobile-nav-height;\n    margin: 1rem 0;\n    width: 95vw;\n\n    display: flex;\n    flex-flow: column nowrap;\n  }\n\n  p{\n    align-self: flex-start;\n    font-size: 0.7em;\n    font-style: italic;\n    @include respond(tab-port){\n      margin-left: 3rem;\n    }\n\n    @include respond(phone){\n      margin-left: 0.5rem;\n    }\n  }\n  ////////////////////////////////////////////////\n  .food-section{\n  \n    position: relative;\n    margin-bottom: 1rem;\n\n    grid-column: 1 / span 2;\n    \n    display: flex;\n    flex-flow: column;\n    align-items: stretch;\n\n    border-top: 3px solid $portland-red;\n    \n    @include respond(tab-port){\n      margin-bottom: 1.5rem;\n      align-items: stretch;\n    }\n\n    .section__title{\n      align-self: flex-start;\n      text-transform: capitalize;\n      font-weight: 700;\n      font-size: 0.95em;\n      text-align: center;\n      color: $portland-red;\n  \n      padding: 3rem 2rem 0rem 0.5rem;\n  \n      @include respond(big-desk){\n        font-size: 1.2em;\n      }\n\n      @include respond(phone){\n        font-size: 1.2em;\n      }\n    }\n  \n    .section__list{\n\n      display: grid;\n      grid-template-columns: 25% 25% 25% 25%;\n      justify-content: stretch;\n      padding: 1rem;\n      justify-content: center;\n\n      @include respond(tab-port){\n        display: grid;\n        grid-template-columns: 40vw 40vw;\n        justify-content: space-around;\n        align-items: stretch;\n        \n        text-align: start;\n      }\n      \n      @include respond(phone){\n        display: flex;\n        flex-flow: column nowrap;\n        align-items: stretch;\n      }\n    }\n  }\n\n  section.snacks .section__title{\n    color: $potential-blue;\n  }\n  section.omnivores .section__title{\n    color: $portland-red;\n  }\n  section.herbivores .section__title{\n    color: $maybe-green;\n  }\n\n  // positions \"sides\" beside \"sweets\"\n  sectio